import React, { useState, useEffect, useRef } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
  Title,
} from 'chart.js';

import '../styles/Section.css';
import '../styles/CashFirstMasterPlan.css';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
  Title
);

/**
 * Utility: generates net worth data over a timeline (startAge..endAge).
 * - monthlyInvest: how much user invests monthly 
 * - annualReturn: base annual return in %
 * - synergy: additional synergy factor (tax, discipline, etc.)
 * - shockEvent: if true, we simulate a recession around halfway 
 *   that drops net worth by some % and reduces annualReturn for ~2 years
 */
function generatePlanData({
  label,
  startAge,
  endAge,
  monthlyInvest,
  annualReturn,
  synergy,
  shockEvent,
}) {
  const monthlyRate = (annualReturn + synergy) / 100 / 12;
  let netWorth = 0;
  const dataPoints = [];
  
  const totalYears = endAge - startAge;
  const totalMonths = totalYears * 12;
  
  // For shock simulation
  const shockMonthStart = Math.floor(totalMonths / 2) - 6;  // start ~6 months before midpoint
  const shockMonthEnd = Math.floor(totalMonths / 2) + 18;   // end ~18 months after midpoint
  const shockDropPercent = 0.20; // immediate 20% drop at shockMonthStart
  const shockReturnPenalty = 3;  // reduce annual return by 3% during shock period

  for (let month = 0; month <= totalMonths; month++) {
    // Yearly data point every 12 months
    if (month % 12 === 0) {
      const yearsFromStart = month / 12;
      dataPoints.push({ x: yearsFromStart, y: Math.round(netWorth) });
    }
    if (month < totalMonths) {
      // add monthly investment
      netWorth += monthlyInvest;

      // handle shock logic
      if (shockEvent && month === shockMonthStart && netWorth > 0) {
        // immediate drop in net worth
        netWorth *= (1 - shockDropPercent); 
      }

      // adjusted monthly rate if in shock period
      let effectiveMonthlyRate = monthlyRate;
      if (shockEvent && month >= shockMonthStart && month <= shockMonthEnd) {
        // reduce annualReturn by 'shockReturnPenalty' => monthly penalty
        const penaltyMonthlyRate = (shockReturnPenalty / 100) / 12;
        effectiveMonthlyRate = Math.max(0, monthlyRate - penaltyMonthlyRate);
      }

      // monthly compounding
      netWorth *= (1 + effectiveMonthlyRate);
    }
  }

  return { label, data: dataPoints };
}

const CashFirstMasterPlan = () => {
  /* ===============================
     1) STATE
  =============================== */
  const [startAge, setStartAge] = useState(25);
  const [endAge, setEndAge] = useState(65);

  // We'll let the user tweak synergy for minimal, partial, full scenario:
  const [minimalSynergy, setMinimalSynergy] = useState(0);  // minimal discipline
  const [partialSynergy, setPartialSynergy] = useState(3);  // moderate
  const [fullSynergy, setFullSynergy] = useState(6);        // maximum synergy (tax, discipline, etc.)

  // base annual return
  const [annualReturn, setAnnualReturn] = useState(6);

  // monthly invests for each scenario
  const [minimalInvest, setMinimalInvest] = useState(500);
  const [partialInvest, setPartialInvest] = useState(1000);
  const [fullInvest, setFullInvest] = useState(2000);

  // shock event toggle
  const [shockEvent, setShockEvent] = useState(false);

  const [chartData, setChartData] = useState(null);

  // We'll create references for gradient fills
  const chartRef = useRef(null);

  /* ===============================
     2) GENERATE CHART DATA
  =============================== */
  useEffect(() => {
    const scenario1 = generatePlanData({
      label: 'Minimal Adoption',
      startAge,
      endAge,
      monthlyInvest: minimalInvest,
      annualReturn,
      synergy: minimalSynergy,
      shockEvent,
    });
    const scenario2 = generatePlanData({
      label: 'Partial Adoption',
      startAge,
      endAge,
      monthlyInvest: partialInvest,
      annualReturn,
      synergy: partialSynergy,
      shockEvent,
    });
    const scenario3 = generatePlanData({
      label: 'Full Master Plan',
      startAge,
      endAge,
      monthlyInvest: fullInvest,
      annualReturn,
      synergy: fullSynergy,
      shockEvent,
    });

    // Build the dataset array with advanced gradient fills
    // We'll define gradients in the effect after the chartRef is rendered
    const gradient1 = chartRef.current 
      ? createGradient(chartRef.current.ctx, '#ff5733')
      : '#ff573344';
    const gradient2 = chartRef.current 
      ? createGradient(chartRef.current.ctx, '#FFD700')
      : '#FFD70044';
    const gradient3 = chartRef.current 
      ? createGradient(chartRef.current.ctx, '#008080')
      : '#00808044';

    const finalData = {
      datasets: [
        {
          label: scenario1.label,
          data: scenario1.data,
          borderColor: '#ff5733',
          backgroundColor: gradient1,
          fill: true,
          tension: 0.3,
          parsing: { xAxisKey: 'x', yAxisKey: 'y' },
        },
        {
          label: scenario2.label,
          data: scenario2.data,
          borderColor: '#FFD700',
          backgroundColor: gradient2,
          fill: true,
          tension: 0.3,
          parsing: { xAxisKey: 'x', yAxisKey: 'y' },
        },
        {
          label: scenario3.label,
          data: scenario3.data,
          borderColor: '#008080',
          backgroundColor: gradient3,
          fill: true,
          tension: 0.3,
          parsing: { xAxisKey: 'x', yAxisKey: 'y' },
        },
      ],
    };

    setChartData(finalData);
  }, [
    startAge,
    endAge,
    minimalSynergy,
    partialSynergy,
    fullSynergy,
    annualReturn,
    minimalInvest,
    partialInvest,
    fullInvest,
    shockEvent
  ]);

  /* ===============================
     3) CHART OPTIONS
  =============================== */
  const chartOptions = {
    responsive: true,
    plugins: {
      title: {
        display: true,
        text: 'Cash-First Master Plan (Advanced Projection)',
        font: { size: 16 },
      },
      tooltip: {
        callbacks: {
          label: (ctx) => {
            const val = ctx.parsed.y || 0;
            return `${ctx.dataset.label}: $${val.toLocaleString()}`;
          }
        }
      },
      legend: { position: 'bottom' },
    },
    scales: {
      x: {
        type: 'linear',
        title: { display: true, text: 'Years from Start Age' },
        min: 0,
        suggestedMax: endAge - startAge,
        ticks: {
          stepSize: 5,
        },
      },
      y: {
        beginAtZero: true,
        title: { display: true, text: 'Net Worth (USD)' },
        ticks: {
          callback: (val) => `$${val.toLocaleString()}`,
        },
      },
    }
  };

  /* ===============================
     4) HELPER: CREATE GRADIENT
     This function creates a vertical gradient fill
     that transitions from transparent near the top
     to a stronger color at the bottom.
  =============================== */
  const createGradient = (ctx, color) => {
    const gradient = ctx.createLinearGradient(0, 0, 0, ctx.canvas.height);
    gradient.addColorStop(0, `${color}00`);  // near top, transparent
    gradient.addColorStop(1, `${color}AA`);  // near bottom, more solid
    return gradient;
  };

  /* ===============================
     5) RENDER
  =============================== */
  return (
    <section id="cash-first-master-plan" className="section masterplan-section">
      <h2>Your Cash-First Master Plan: Taking It to the Next Level</h2>
      <p>
        Here is an <strong>advanced projection</strong> of how three lifestyles—Minimal, 
        Partial, and Full Cash-First—might affect net worth over time. We include a 
        <em>midlife shock event</em> (like a recession) if you toggle it on. 
        Notice how discipline and synergy factors (tax efficiency, lifestyle optimization) 
        drastically shape outcomes. Only about 1% of people stick to the full plan, 
        but they see rewards beyond typical comprehension.
      </p>

      <div className="masterplan-content">
        {/* === EDUCATION BLOCK === */}
        <div className="masterplan-education">
          <h3>When Everyone Else Gives Up...</h3>
          <p>
            Friends might upgrade cars on credit, skip out on monthly investing, 
            or let rising lifestyle costs eat away their potential. 
            The <strong>tiny minority</strong> who persist—shunning debt, 
            seizing tax benefits, and investing faithfully—inevitably stand 
            out years down the road.
          </p>
          <h3>A Midlife Shock?</h3>
          <p>
            Life can throw recessions, job losses, or medical emergencies at you. 
            Those with minimal debt, emergency funds, and discipline 
            fare far better—and can sometimes capitalize on market dips.
          </p>
        </div>

        {/* === ADVANCED SLIDERS & SWITCHES === */}
        <div className="masterplan-calculator">
          <h3>Scenario Explorer</h3>
          <p>
            Adjust the synergy (discipline/tax strategies) and monthly invests 
            for each scenario. Toggle the shock event to see how each plan 
            recovers from a recession.
          </p>

          <div className="calc-inputs">
            {/* Age Range */}
            <div className="input-group">
              <label htmlFor="startAge">
                Start Age: <strong>{startAge}</strong>
              </label>
              <input
                type="range"
                id="startAge"
                min="18"
                max="50"
                step="1"
                value={startAge}
                onChange={(e) => setStartAge(Number(e.target.value))}
              />
            </div>

            <div className="input-group">
              <label htmlFor="endAge">
                Retirement Age: <strong>{endAge}</strong>
              </label>
              <input
                type="range"
                id="endAge"
                min={startAge + 1}
                max="75"
                step="1"
                value={endAge}
                onChange={(e) => setEndAge(Number(e.target.value))}
              />
            </div>

            {/* Annual Return */}
            <div className="input-group">
              <label htmlFor="annualReturn">
                Base Annual Return: <strong>{annualReturn}%</strong>
              </label>
              <input
                type="range"
                id="annualReturn"
                min="1"
                max="12"
                step="1"
                value={annualReturn}
                onChange={(e) => setAnnualReturn(Number(e.target.value))}
              />
            </div>

            {/* Minimal synergy + invests */}
            <div className="input-group">
              <label htmlFor="minimalInvest">
                Minimal Plan Monthly: <strong>${minimalInvest}</strong>
              </label>
              <input
                type="range"
                id="minimalInvest"
                min="100"
                max="3000"
                step="100"
                value={minimalInvest}
                onChange={(e) => setMinimalInvest(Number(e.target.value))}
              />
            </div>
            <div className="input-group">
              <label htmlFor="minimalSynergy">
                Minimal Synergy: <strong>{minimalSynergy}</strong>/10
              </label>
              <input
                type="range"
                id="minimalSynergy"
                min="0"
                max="10"
                step="1"
                value={minimalSynergy}
                onChange={(e) => setMinimalSynergy(Number(e.target.value))}
              />
            </div>

            {/* Partial synergy + invests */}
            <div className="input-group">
              <label htmlFor="partialInvest">
                Partial Plan Monthly: <strong>${partialInvest}</strong>
              </label>
              <input
                type="range"
                id="partialInvest"
                min="200"
                max="5000"
                step="100"
                value={partialInvest}
                onChange={(e) => setPartialInvest(Number(e.target.value))}
              />
            </div>
            <div className="input-group">
              <label htmlFor="partialSynergy">
                Partial Synergy: <strong>{partialSynergy}</strong>/10
              </label>
              <input
                type="range"
                id="partialSynergy"
                min="0"
                max="10"
                step="1"
                value={partialSynergy}
                onChange={(e) => setPartialSynergy(Number(e.target.value))}
              />
            </div>

            {/* Full synergy + invests */}
            <div className="input-group">
              <label htmlFor="fullInvest">
                Full Plan Monthly: <strong>${fullInvest}</strong>
              </label>
              <input
                type="range"
                id="fullInvest"
                min="300"
                max="10000"
                step="100"
                value={fullInvest}
                onChange={(e) => setFullInvest(Number(e.target.value))}
              />
            </div>
            <div className="input-group">
              <label htmlFor="fullSynergy">
                Full Synergy: <strong>{fullSynergy}</strong>/10
              </label>
              <input
                type="range"
                id="fullSynergy"
                min="0"
                max="10"
                step="1"
                value={fullSynergy}
                onChange={(e) => setFullSynergy(Number(e.target.value))}
              />
            </div>
          </div>

          {/* Shock Toggle */}
          <div className="shock-toggle">
            <label>
              <input
                type="checkbox"
                checked={shockEvent}
                onChange={() => setShockEvent(!shockEvent)}
              /> 
              Simulate Recession Midway
            </label>
          </div>

          <div className="calc-results">
            {/* We'll store the chart reference in chartRef to create gradients */}
            <div className="masterplan-chart-container">
              {chartData && (
                <Line
                  ref={chartRef}
                  data={chartData}
                  options={chartOptions}
                  height={400}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

/* 
  We'll define the createGradient function in the 
  effect if we want dynamic gradients. But for simplicity, 
  we've placed it inline above. 
*/

export default CashFirstMasterPlan;