import React, { useState, useEffect } from 'react';
import { Doughnut } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  ArcElement,
  Tooltip,
  Legend
} from 'chart.js';

import '../styles/Section.css';     // Base .section styling
import '../styles/LivingOnLess.css'; // Specific overrides

ChartJS.register(ArcElement, Tooltip, Legend);

const LivingOnLess = () => {
  /* ===========================
     1) STATE
  ============================ */
  const [monthlyIncome, setMonthlyIncome] = useState(4000);
  const [housing, setHousing] = useState(1200);
  const [food, setFood] = useState(500);
  const [transport, setTransport] = useState(200);
  const [entertainment, setEntertainment] = useState(200);
  const [others, setOthers] = useState(300);

  const [totalSpending, setTotalSpending] = useState(0);

  /* ===========================
     2) HELPER
  ============================ */
  const formatCurrency = (value) => `$${Math.round(value).toLocaleString()}`;

  /* ===========================
     3) CALCULATIONS
  ============================ */
  useEffect(() => {
    // Sum up all the spending categories
    const total = housing + food + transport + entertainment + others;
    setTotalSpending(total);
  }, [housing, food, transport, entertainment, others]);

  /* ===========================
     4) DOUGHNUT CHART DATA
  ============================ */
  const chartData = {
    labels: ['Housing', 'Food', 'Transport', 'Entertainment', 'Others'],
    datasets: [
      {
        data: [housing, food, transport, entertainment, others],
        backgroundColor: ['#008080', '#ff5733', '#FFD700', '#005959', '#3d3d3d'],
      }
    ],
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      tooltip: {
        callbacks: {
          label: (context) => {
            const val = context.parsed || 0;
            return `${context.label}: ${formatCurrency(val)}`;
          },
        },
      },
      legend: {
        position: 'bottom',
      },
    },
  };

  /* ===========================
     5) RENDER
  ============================ */
  return (
    <section id="living-on-less" className="section living-section">
      <h2>Living on Less, Thriving with More: The Art of Spending Below Your Means</h2>
      <p>
        Spending below your means is the foundation of building wealth without anxiety. 
        By tracking expenses and making mindful cuts where possible, you free up money 
        for savings, investments, or the experiences that truly matter to you. Use the 
        sliders below to adjust your monthly budget and see if you’re living within your 
        income—or overspending.
      </p>

      <div className="living-content">
        {/* ==== EDUCATION BLOCK ==== */}
        <div className="living-education">
          <h3>Why Live on Less?</h3>
          <p>
            <strong>Less Stress:</strong> No more juggling bills or worrying about 
            credit card limits.  
          </p>
          <p>
            <strong>More Freedom:</strong> Accumulate savings to seize opportunities, 
            from travel to starting a business, without debt weighing you down.
          </p>
          <p>
            <strong>Better Focus:</strong> Simplifying your financial life means fewer 
            distractions, so you can prioritize what genuinely enriches your life.
          </p>
        </div>

        {/* ==== CALCULATOR & DOUGHNUT ==== */}
        <div className="living-calculator">
          <h3>Monthly Budget Calculator</h3>
          <p>
            Adjust each spending category below. The doughnut chart will update to show your 
            spending distribution. Compare <strong>monthly income</strong> with 
            <strong> total spending</strong> to see if you’re living below your means.
          </p>

          <div className="calc-inputs">
            {/* Monthly Income */}
            <div className="input-group">
              <label htmlFor="monthlyIncome">
                Monthly Income: <strong>{formatCurrency(monthlyIncome)}</strong>
              </label>
              <input
                type="range"
                id="monthlyIncome"
                min="1000"
                max="20000"
                step="500"
                value={monthlyIncome}
                onChange={(e) => setMonthlyIncome(Number(e.target.value))}
              />
            </div>

            {/* Housing */}
            <div className="input-group">
              <label htmlFor="housing">
                Housing: <strong>{formatCurrency(housing)}</strong>
              </label>
              <input
                type="range"
                id="housing"
                min="0"
                max="5000"
                step="100"
                value={housing}
                onChange={(e) => setHousing(Number(e.target.value))}
              />
            </div>

            {/* Food */}
            <div className="input-group">
              <label htmlFor="food">
                Food: <strong>{formatCurrency(food)}</strong>
              </label>
              <input
                type="range"
                id="food"
                min="0"
                max="2000"
                step="50"
                value={food}
                onChange={(e) => setFood(Number(e.target.value))}
              />
            </div>

            {/* Transport */}
            <div className="input-group">
              <label htmlFor="transport">
                Transport: <strong>{formatCurrency(transport)}</strong>
              </label>
              <input
                type="range"
                id="transport"
                min="0"
                max="1000"
                step="50"
                value={transport}
                onChange={(e) => setTransport(Number(e.target.value))}
              />
            </div>

            {/* Entertainment */}
            <div className="input-group">
              <label htmlFor="entertainment">
                Entertainment: <strong>{formatCurrency(entertainment)}</strong>
              </label>
              <input
                type="range"
                id="entertainment"
                min="0"
                max="1000"
                step="50"
                value={entertainment}
                onChange={(e) => setEntertainment(Number(e.target.value))}
              />
            </div>

            {/* Others */}
            <div className="input-group">
              <label htmlFor="others">
                Others: <strong>{formatCurrency(others)}</strong>
              </label>
              <input
                type="range"
                id="others"
                min="0"
                max="2000"
                step="50"
                value={others}
                onChange={(e) => setOthers(Number(e.target.value))}
              />
            </div>
          </div>

          {/* RESULTS */}
          <div className="calc-results">
            <h4>Spending Overview</h4>
            <p>
              <strong>Total Spending:</strong> {formatCurrency(totalSpending)}
            </p>
            <p>
              <strong>Remaining Income:</strong>{' '}
              {formatCurrency(monthlyIncome - totalSpending)}
            </p>

            {totalSpending > monthlyIncome ? (
              <p>
                <em style={{ color: 'red' }}>
                  You’re spending <strong>above</strong> your means. Consider reducing 
                  certain categories to free up funds for savings!
                </em>
              </p>
            ) : (
              <p>
                <em style={{ color: 'green' }}>
                  Good job! You’re living <strong>below</strong> your means, leaving 
                  extra cash for savings or investments.
                </em>
              </p>
            )}

            {/* DOUGHNUT CHART */}
            <div className="living-chart-container">
              <Doughnut data={chartData} options={chartOptions} />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default LivingOnLess;