import React from 'react';
import '../styles/Section.css';

const Section = ({ id, title, content }) => (
  <section id={id} className="section">
    <h2>{title}</h2>
    {Array.isArray(content) ? (
      <ul>
        {content.map((item, index) => (
          <li key={index}>{item}</li>
        ))}
      </ul>
    ) : (
      <p>{content}</p>
    )}
  </section>
);

export default Section;