const EnhancedPrinciples = () => {
  const [expandedPrinciple, setExpandedPrinciple] = useState(null);

  const togglePrinciple = (index) => {
    setExpandedPrinciple(expandedPrinciple === index ? null : index);
  };

  return (
    <section id="principles" className="section">
      <h2>Core Principles of the Cash-First Financial Strategy</h2>
      <p>
        These enhanced principles go beyond simple suggestions to include
        specific steps, real-world examples, and tangible benefits. Click on each
        principle below to learn why it matters and how to integrate it into
        your financial life.
      </p>

      <div className="principles-container">
        {enhancedPrinciplesData.map((principle, index) => (
          <div key={index} className="principle">
            <h3 onClick={() => togglePrinciple(index)}>
              {principle.title}
              <span>
                {expandedPrinciple === index ? '−' : '+'}
              </span>
            </h3>

            {/* Always show a short summary, even if collapsed */}
            <p>{principle.summary}</p>

            {/* Show additional detail if expanded */}
            {expandedPrinciple === index && (
              <div className="principle-details">
                <h4>Key Benefits</h4>
                <ul className="benefits-list">
                  {principle.benefits.map((benefit, bIdx) => (
                    <li key={bIdx}>{benefit}</li>
                  ))}
                </ul>

                <h4>Implementation Tips</h4>
                <ul className="implementation-list">
                  {principle.implementation.map((tip, tIdx) => (
                    <li key={tIdx}>{tip}</li>
                  ))}
                </ul>

                <h4>Real-World Example</h4>
                <p className="example-text">{principle.example}</p>
              </div>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};

export default EnhancedPrinciples;