import React, { useState, useEffect } from 'react';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import {
  Chart as ChartJS,
  BarElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  LineElement,
  PointElement,
  Filler,
} from 'chart.js';
import { Bar /* or Line if needed */ } from 'react-chartjs-2';
import '../styles/Tools.css';
import '../styles/Section.css'; // If `Tools` depends on section styling

// Register chart elements
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Tooltip,
  Legend,
  Filler,
  ChartDataLabels
);

const Tools = () => {
  /* ===========================================
     1) STATE & HOOKS
  =========================================== */
  const [loanAmount, setLoanAmount] = useState(300000);
  const [interestRate, setInterestRate] = useState(7.2);
  const [loanTerm, setLoanTerm] = useState(30);
  const [downPayment, setDownPayment] = useState(300000 * 0.05); // 5% default
  const [results, setResults] = useState(null);
  const [savings, setSavings] = useState(null);
  const [tooltipMessage, setTooltipMessage] = useState('');

  // Helper to format currency
  const formatCurrency = (value) => {
    return `$${Math.round(value).toLocaleString()}`;
  };

  /* ===========================================
     2) MORTGAGE CALCULATIONS
  =========================================== */
  const calculateMortgage = () => {
    const principal = loanAmount - downPayment;
    // Increase down payment by 50% for the shorter loan scenario
    const shorterDownPayment = downPayment * 1.5;
    const shorterPrincipal = loanAmount - shorterDownPayment;

    const monthlyRate = interestRate / 100 / 12;
    const numberOfPayments = loanTerm * 12;

    // For easy referencing in the charts
    const downPaymentPercentage = (downPayment / loanAmount) * 100;
    const shorterDownPaymentPercentage = (shorterDownPayment / loanAmount) * 100;

    // ~~~ Current Loan Calculation ~~~
    const monthlyPayment =
      (principal * monthlyRate) /
      (1 - Math.pow(1 + monthlyRate, -numberOfPayments));
    const totalPaid = monthlyPayment * numberOfPayments;
    const totalInterest = totalPaid - principal;

    // PMI (Current Loan) --------------------------------
    let remainingBalance = principal;
    let totalPmiCost = 0;
    const annualPmiRate = 0.0075; // 0.75%

    if (downPaymentPercentage < 20) {
      for (let year = 1; year <= loanTerm; year++) {
        if (remainingBalance <= loanAmount * 0.8) break; // PMI drops at 80% LTV
        totalPmiCost += remainingBalance * annualPmiRate;
        remainingBalance -= principal / loanTerm; // Approx. principal paid per year
      }
    }

    // ~~~ Shorter-Term Mortgage ~~~
    const shorterTerm = Math.ceil(loanTerm / 2);
    const shorterTermPayments = shorterTerm * 12;
    const shorterMonthlyPayment =
      (shorterPrincipal * monthlyRate) /
      (1 - Math.pow(1 + monthlyRate, -shorterTermPayments));
    const shorterTotalPaid = shorterMonthlyPayment * shorterTermPayments;
    const shorterTotalInterest = shorterTotalPaid - shorterPrincipal;

    // PMI (Shorter Loan) --------------------------------
    let shorterRemainingBalance = shorterPrincipal;
    let shorterTotalPmiCost = 0;

    if (shorterDownPaymentPercentage < 20) {
      for (let year = 1; year <= shorterTerm; year++) {
        if (shorterRemainingBalance <= loanAmount * 0.8) break;
        shorterTotalPmiCost += shorterRemainingBalance * annualPmiRate;
        shorterRemainingBalance -= shorterPrincipal / shorterTerm;
      }
    }

    setResults({
      monthlyPayment: monthlyPayment.toFixed(2),
      totalPaid: totalPaid.toFixed(2),
      totalInterest: totalInterest.toFixed(2),
      totalPmiCost: totalPmiCost.toFixed(2),
    });

    setSavings({
      shorterTerm,
      shorterMonthlyPayment: shorterMonthlyPayment.toFixed(2),
      shorterTotalPaid: shorterTotalPaid.toFixed(2),
      shorterTotalInterest: shorterTotalInterest.toFixed(2),
      shorterTotalPmiCost: shorterTotalPmiCost.toFixed(2),
      shorterDownPayment: shorterDownPayment.toFixed(2),
    });
  };

  useEffect(() => {
    calculateMortgage();
  }, [loanAmount, interestRate, loanTerm, downPayment]);

  /* ===========================================
     3) PRE-SET SCENARIOS
  =========================================== */
  const loadScenario = (scenario) => {
    const scenarios = {
      starter: { amount: 200000, rate: 7.3, term: 30, down: 10000 },
      mid: { amount: 500000, rate: 6.9, term: 30, down: 25000 },
      luxury: { amount: 800000, rate: 6.5, term: 30, down: 40000 },
    };
    setLoanAmount(scenarios[scenario].amount);
    setInterestRate(scenarios[scenario].rate);
    setLoanTerm(scenarios[scenario].term);
    setDownPayment(scenarios[scenario].down);
  };

  /* ===========================================
     4) TOOLTIP HANDLERS
  =========================================== */
  const handleMouseEnter = (message) => {
    setTooltipMessage(message);
  };

  const handleMouseLeave = () => {
    setTooltipMessage('');
  };

  /* ===========================================
     5) RENDER
  =========================================== */
  const downPaymentPercentage = ((downPayment / loanAmount) * 100).toFixed(0);

  return (
    <section id="tools" className="section tools-section">
      <h2>Mortgage Interest Visualizer</h2>
      <p>
        Adjust the sliders below to see how different loan terms and interest rates
        impact your total mortgage cost. Use the pre-set scenarios for quick insights!
      </p>

      {/* ~~~ SCENARIO BUTTONS ~~~ */}
      <div className="tools-scenarios">
        <button onClick={() => loadScenario('starter')}>🏡 Starter Home</button>
        <button onClick={() => loadScenario('mid')}>🏠 Mid-Level Home</button>
        <button onClick={() => loadScenario('luxury')}>🏘️ Luxury Home</button>
      </div>

      {/* ~~~ INPUT SLIDERS ~~~ */}
      <div className="tools-inputs">
        <div className="input-group">
          <label htmlFor="loanAmount">
            🏦 Loan Amount:{' '}
            <strong>{formatCurrency(loanAmount)}</strong>
          </label>
          <input
            id="loanAmount"
            type="range"
            min="50000"
            max="1000000"
            step="5000"
            value={loanAmount}
            onChange={(e) => setLoanAmount(Number(e.target.value))}
            onMouseEnter={() =>
              handleMouseEnter('The amount you are borrowing from the bank.')
            }
            onMouseLeave={handleMouseLeave}
          />
        </div>

        <div className="input-group">
          <label htmlFor="interestRate">
            📉 Interest Rate:{' '}
            <strong>{interestRate.toFixed(2)}%</strong>
          </label>
          <input
            id="interestRate"
            type="range"
            min="1"
            max="10"
            step="0.1"
            value={interestRate}
            onChange={(e) => setInterestRate(Number(e.target.value))}
            onMouseEnter={() =>
              handleMouseEnter(
                'The percentage charged by the lender for borrowing money.'
              )
            }
            onMouseLeave={handleMouseLeave}
          />
        </div>

        <div className="input-group">
          <label htmlFor="loanTerm">
            📆 Loan Term: <strong>{loanTerm} years</strong>
          </label>
          <input
            id="loanTerm"
            type="range"
            min="5"
            max="30"
            step="1"
            value={loanTerm}
            onChange={(e) => setLoanTerm(Number(e.target.value))}
            onMouseEnter={() =>
              handleMouseEnter(
                'The total number of years you will be making payments.'
              )
            }
            onMouseLeave={handleMouseLeave}
          />
        </div>

        {/* ~~~ DOWN PAYMENT SLIDER ~~~ */}
        <div className="input-group">
          <label htmlFor="downPayment">
            💰 Down Payment:{' '}
            <strong>{formatCurrency(downPayment)}</strong> ({downPaymentPercentage}%)
          </label>
          <input
            id="downPayment"
            type="range"
            min="0"
            max={loanAmount}
            step="5000"
            value={downPayment}
            onChange={(e) => setDownPayment(Number(e.target.value))}
            onMouseEnter={() =>
              handleMouseEnter(
                'Your initial upfront payment. 20% removes PMI.'
              )
            }
            onMouseLeave={handleMouseLeave}
          />
        </div>
      </div>

      {/* ~~~ TOOLTIP ~~~ */}
      {tooltipMessage && <div className="tooltip">{tooltipMessage}</div>}

      {/* ~~~ RESULTS & CHART ~~~ */}
      {results && savings && (
        <div>
          <h3>📊 Mortgage Breakdown</h3>
          <div className="tools-chart-container">
            <Bar
              data={{
                labels: [
                  `Current Term (${loanTerm} Years)`,
                  `Shorter Term (${savings.shorterTerm} Years)`,
                  'Cash Purchase',
                ],
                datasets: [
                  {
                    label: 'Down Payment',
                    data: [
                      downPayment,
                      downPayment,
                      loanAmount, // For cash purchase, the entire amount
                    ],
                    backgroundColor: '#005959',
                    stack: 'Stack 0',
                  },
                  {
                    label: 'Principal',
                    data: [
                      loanAmount - downPayment,
                      loanAmount - downPayment,
                      0,
                    ],
                    backgroundColor: '#008080',
                    stack: 'Stack 0',
                  },
                  {
                    label: 'Interest',
                    data: [
                      results.totalInterest,
                      savings.shorterTotalInterest,
                      0,
                    ],
                    backgroundColor: '#ff5733',
                    stack: 'Stack 0',
                  },
                  {
                    label: 'PMI (If Applicable)',
                    data: [
                      results.totalPmiCost,
                      savings.shorterTotalPmiCost,
                      0,
                    ],
                    backgroundColor: '#FFD700',
                    stack: 'Stack 0',
                  },
                ],
              }}
              options={{
                responsive: true,
                plugins: {
                  tooltip: {
                    callbacks: {
                      label: (context) =>
                        `${context.dataset.label}: ${formatCurrency(
                          context.raw
                        )}`,
                    },
                  },
                  datalabels: {
                    display: (context) => context.datasetIndex === 3,
                    color: '#000',
                    font: { size: 14, weight: 'bold' },
                    anchor: 'end',
                    align: 'end',
                    formatter: (_, context) => {
                      const down = Number(
                        context.chart.data.datasets[0].data[context.dataIndex]
                      );
                      const principal = Number(
                        context.chart.data.datasets[1].data[context.dataIndex]
                      );
                      const interest = Number(
                        context.chart.data.datasets[2].data[context.dataIndex]
                      );
                      const pmi = Number(
                        context.chart.data.datasets[3].data[context.dataIndex]
                      );
                      return formatCurrency(down + principal + interest + pmi);
                    },
                  },
                },
                scales: {
                  y: {
                    beginAtZero: true,
                    suggestedMax: Number(results.totalPaid) * 1.1, // ~10% buffer
                    title: { display: true, text: 'Total Loan Cost ($)' },
                    ticks: {
                      callback: (value) => formatCurrency(value),
                    },
                  },
                },
              }}
            />
          </div>

          {/* ~~~ COMPARISON BOXES ~~~ */}
          <h3>📌 Key Comparisons</h3>
          <div className="tools-comparison">
            <div className="comparison-box">
              <h4>📅 {loanTerm}-Year Term</h4>
              <p>
                🏦 Down Payment:{' '}
                <strong>{formatCurrency(downPayment)}</strong> ({downPaymentPercentage}%)
              </p>
              <p>
                💰 Monthly:{' '}
                <strong>{formatCurrency(results.monthlyPayment)}</strong>
              </p>
              <p>
                📈 Interest:{' '}
                <strong>{formatCurrency(results.totalInterest)}</strong>
              </p>
              <p>
                ⚠️ PMI Cost:{' '}
                <strong>{formatCurrency(results.totalPmiCost)}</strong>
              </p>
              <p>
                💵 Total Paid:{' '}
                <strong>{formatCurrency(results.totalPaid)}</strong>
              </p>
            </div>

            <div className="comparison-box">
              <h4>⚡ {savings.shorterTerm}-Year Term</h4>
              <p>
                🏦 Down Payment:{' '}
                <strong>{formatCurrency(savings.shorterDownPayment)}</strong> (
                {(downPaymentPercentage * 1.5).toFixed(0)}%)
              </p>
              <p>
                💰 Monthly:{' '}
                <strong>{formatCurrency(savings.shorterMonthlyPayment)}</strong>
              </p>
              <p>
                📉 Interest:{' '}
                <strong>{formatCurrency(savings.shorterTotalInterest)}</strong>
              </p>
              <p>
                ⚠️ PMI Cost:{' '}
                <strong>{formatCurrency(savings.shorterTotalPmiCost)}</strong>
              </p>
              <p>
                💵 Total Paid:{' '}
                <strong>{formatCurrency(savings.shorterTotalPaid)}</strong>
              </p>
            </div>
          </div>

          {/* ~~~ SUMMARY ~~~ */}
          <h3>Key Takeaways</h3>
          <div className="tools-summary">
            <p>
              If you can contribute an extra{' '}
              <strong>
                {formatCurrency(
                  savings.shorterMonthlyPayment - results.monthlyPayment
                )}
              </strong>{' '}
              each month and increase your down payment to{' '}
              <strong>
                {formatCurrency(savings.shorterDownPayment)} (
                {(downPaymentPercentage * 1.5).toFixed(0)}
                %)
              </strong>
              , you can opt for the shorter{' '}
              <strong>{savings.shorterTerm}-year term</strong> and save{' '}
              <strong>
                {formatCurrency(
                  results.totalInterest - savings.shorterTotalInterest
                )}
              </strong>{' '}
              in interest over the life of the loan.
            </p>

            <p>
              Additionally, your <strong>PMI cost over {loanTerm} years</strong>{' '}
              is{' '}
              <strong>{formatCurrency(results.totalPmiCost)}</strong>. If you
              increase your{' '}
              <strong>down payment to 20%</strong>, PMI disappears—saving you{' '}
              <strong>{formatCurrency(results.totalPmiCost)} instantly!</strong>
            </p>

            <p>
              If you can't save up 20%, switching to the{' '}
              <strong>{savings.shorterTerm}-year term</strong> will not only
              help you pay off your loan faster but also{' '}
              <strong>
                reduce PMI payments by{' '}
                {formatCurrency(
                  results.totalPmiCost - savings.shorterTotalPmiCost
                )}
              </strong>
              .
            </p>
          </div>
        </div>
      )}
    </section>
  );
};

export default Tools;